#blog {
  min-height: calc(100vh - 10rem);
  display: flex;
  flex-direction: column;

  .locations {
    width: 85%;
    margin: auto;
    text-align: center;
    padding-top: 2rem;

    .locationsTitle{
      font-size: 2rem;
    }
    .locationsDescription{
      margin-top: 1rem;
      font-size: 1.25rem;
      font-weight: 600;
    }
    .row {
      margin-top: 3%;
      display: flex;
      justify-content: space-between;
      .classLocation {
        flex-basis: 32.5%;
        border-radius: 10px;
        margin-bottom: 3rem;
        position: relative;
        overflow: hidden;
      }
      .classLocation img {
        width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
      }
    }

    .layer {
      background: transparent;
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      transition: 0.5s;
    }
    .layer:hover {
      background: #9090aa;
      opacity: 0.9;
      transition: 0.5s;
    }
    .layer h3{
      width: 100%;
      font-weight: 500;
      margin-top: 5rem;
      color: #FAF9F6;
      font-size: 2rem;
      bottom: 0.5rem;
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      opacity: 0;
      transition: 0.5s;
    }
    .layer:hover h3{
      bottom: 49%;
      opacity: 1;
    }
    .layer p{
      width: 100%;
      font-weight: 500;
      margin-top: 5rem;
      color: #FAF9F6;
      font-size: 1.25rem;
      bottom: 0.5rem;
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      opacity: 0;
      transition: 0.5s;
    }
    .layer:hover p{
      bottom: 43%;
      opacity: 1;
    }
  }
  .funders{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 2rem;

    .fundersTitle{
      font-size: 2rem;
      margin-bottom: 0.75rem;
      text-align: center;
    }

    .fundersList{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      list-style: none;
      margin: 1rem 2rem;

      .fundersListItem{
        font-size: 1.25rem;
        margin: 0.3rem;
        border-right: 2px solid #6c8832;
        padding-right: 0.3rem;
        font-weight: 500;
      }
    }
  }

  .certificates {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: 32.5%;
    border-radius: 10px;
    margin: 2rem 1rem;
    position: relative;
    overflow: hidden;

    .certificatesRow{
      margin-top: 2%;
      display: flex;
      justify-content: space-evenly;

      .certificate{
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .certificate img{
        background: #9090aa;
        width: 70%;
        height: 90%;
        /* display: block; */
        object-fit: cover;
        margin: 0.2rem;
        padding: 0.25rem;
        border-radius: 1rem;
      }
    }
  }


  @media screen and (max-width:900px) {
    .row {
      .layer h3{
        font-size: 1.8rem;
        margin-bottom: 0.75rem;
      }
      .layer p{
        font-size: 0.8rem;
        margin-top: 0.25rem;
    }
    }
  }
  @media screen and (max-width:560px) {
    .locations{
      .locationsTitle {
        font-size: 2.5rem;
      }

      .locationsDescription{
        font-size: 1rem;
      }
      .row {
        margin-top: 4%;
        flex-direction: column;
      }
    }
    .funders{
      margin: 0.25rem 0.75rem;
      .fundersTitle{
        font-size: 2.25rem;
        margin: 0 0.75rem;
      }
      .fundersList{
        .fundersListItem{
          font-size: 0.75rem;
        }
      }
    }
  }

}
